// get
export function ajaxGet(url = '', data = {}, option = {}, isMock) {
  // 1
  if(arguments[arguments.length-1] != 1) {
    url = 'http://120.78.218.93:7300/mock/5b723f526f609f68edb5e5f9/api' + url
  };
  Indicator.open({
      text: '...',
      spinnerType: 'fading-circle'
  });
	return axios.get(url,data).then((result)=>{
        Indicator.close();
        // code==200
        if(result.data.code == 200){
            return Promise.resolve(result.data);
        }else{
            MessageBox('', result.data.message);
            return Promise.reject(result.data)
        }
	}).catch((result)=>{
        Indicator.close();
        MessageBox('', '');
	})
}

// Post
export function ajaxPost(url = '', data = {}, option = {}, isMock) {
  // 1
  console.info(arguments[arguments.length-1])
  if(arguments[arguments.length-1] != 1) {
    // url = 'http://192.172.30.41:7979' + url
    url = 'http://120.78.218.93:7300/mock/5b723f526f609f68edb5e5f9/api' + url
  };
  Indicator.open({
      text: '...',
      spinnerType: 'fading-circle'
  });
  if(axios.defaults.headers['Content-Type'] === 'application/x-www-form-urlencoded;charset=UTF-8') {
    data = qs.stringify(data, { indices: false });
  }
	return axios.post(url, data, option).then(result => {
		Indicator.close();
        // code==2
        if(result.data.code == 200){
            return Promise.resolve(result.data);
        }else{
            MessageBox('', result.data.message);
            return Promise.reject(result)
        }
	}).catch(res => {
        Indicator.close();
        MessageBox('', '');
	})
}

