export function createSignature(url){
    return ajaxPost('/mdc/store/createSignature',{
        url
    })
}

// 
export function getStoreBylatAndIng(longitude,latitude){
  axios.defaults.headers['Content-Type'] = 'application/json';
    return ajaxPost('/mdc/store/getStoreBylatAndIng',{
      longitude: longitude,
      latitude: latitude,
      pageNum: 1,
      pageSize: 10
    })
}

