ajaxPost(`${BASE_URL}/uac/auth/code/image`,1).then(result => {
    if(!!getToken()) return;
    // return;
    MessageBox({
        title: '',
        message: '<h1><img src="data:image/jpg;base64,' + result.result + '"></h1>',
        showCancelButton: true
    }).then((code) => {
        MessageBox.prompt('').then(({ value, action }) => {
          axios.defaults.headers['Content-Type'] = 'application/json';
          // axios.defaults.headers['Content-Type'] = ' ';
          let data = {
            username: 'yinchao',
            password: '1044510657yc',
            imageCode: value
          };
          data =  qs.stringify(data, { indices: false });
            ajaxPost(`${BASE_URL}/uac/auth/form?username=yinchao&password=1044510657yc&imageCode=${value}`, data, 1).then(result => {
                updateAuthToken(result.result.access_token)
            })
        });
    })
})

