import utils from '../../utils'

const types = {
  //
  POP_STATUS_S: "POP_STATUS_S",
  //
  POP_STATUS_L: "POP_STATUS_L",
  //
  SHOW_FOOTER: "SHOW_FOOTER",
  //tab
  TAB_SELECTED: "TAB_SELECTED",
  //home
  IS_HOME: "IS_HOME",
  //
  NO_SCROLL: "NO_SCROLL",
  //
  SCROLL_TOP: 'SCROLL_TOP',
  //
  LOCATION: 'LOCATION',
  // 
  HOME_SELECTED: 'HOME_SELECTED'
}

const state = {
  popObj: {
    searchPop: false,
    locationPop: false
  },
  hasFooter: true,
  tabSelected: 1,
  isHome: true,
  noScroll: false,
  // 
  // 
  homeSelected: 1,
  location: {
    nodeName: '' || '',
    nodeCode: '' || 1111,
    parentName: '',
    allName: '' || ''
  },
  // 
  scrollTop: 0

}

const getters = {
  isShowFooter: () => {
    return state.hasFooter
  },
  // 
  scrollTop: (state) => {
    let scrollHeight = state.scrollTop;
    if(state.scrollTop > 100){
      scrollHeight = 100;
    } else if (state.scrollTop < 10){
      scrollHeight = 0;
    }
    let tempAph = scrollHeight / 100;
    return tempAph;
  }
}

const actions = {
  add({
    commit,
    state
  }, product) {
    commit(types.ADD_PRODUCT, product)
  }
}

const mutations = {
  //pop
  [types.POP_STATUS_L](state, params) {
    state.popObj.locationPop = params;
  },
  //pop
  [types.POP_STATUS_S](state, params) {
    state.popObj.searchPop = params;
  },
  //
  [types.SHOW_FOOTER](state, params) {
    state.hasFooter = params;
  },
  //
  [types.TAB_SELECTED](state, params) {
    state.tabSelected = params;
  },
  //
  [types.IS_HOME](state, params) {
    state.isHome = params;
  },
  //
  [types.NO_SCROLL](state, params) {
    state.noScroll = params;
  },
  //
  [types.SCROLL_TOP](state, params) {
    state.scrollTop = params;
  },
  //
  [types.LOCATION](state, params) {
    state.location = params;
  },
  // 
  [types.HOME_SELECTED](state, params) {
    let tempObj = {
      'mdc': '1',
      'mdcOrder': '2',
      'mdcMain': '3'
    }
    state.homeSelected = tempObj[params];
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



