import store from '../vuex/store'
import axios from 'axios'
import format from './format'



let instance = axios.create({
  timeout: 50000,
  method: 'get',
});
//,
function showError() {
  store.commit('LOADING', {
    loading: false
  })
  store.commit('TOGGLE_TOAST', {
    toast: true,
    toastMsg: ','
  })
};
//
instance.interceptors.request.use(config => {
  store.commit('LOADING', {
    loading: true,
    msg: ''
  })
  return config
}, error => {
  store.commit('TOGGLE_TOAST', {
    toast: true,
    toastMsg: ''
  })
  return Promise.reject({
    isLogicError: false,
    errorMessage: ","
  })
});
//
instance.interceptors.response.use(response => {
  console.log("%c >>>>>>>", 'color:green', JSON.parse(JSON.stringify(response)))
  store.commit('LOADING_DISABLED', false)
  store.commit('LOADING', {
    loading: false
  })
  try {
    //
    if (!response.errcode) {
      store.commit('WX_USERINFO', response.data);
      return response.data
    } else {
      return Promise.reject({
        isLogicError: true,
        errorMessage: "",
        data: response.data
      })
    }
  } catch (e) {
    console.log(e)
    showError();
    return Promise.reject({
      isLogicError: false,
      errorMessage: ","
    })
  }
}, error => {
  store.commit('LOADING_DISABLED', false)
  showError();
  return Promise.reject({
    isLogicError: false,
    errorMessage: ","
  })
})
export default {
  //
  wxUserInfo1(callback) {
    console.log("", store.state.common.wxUserInfo)
    if (!store.state.common.wxUserInfo) {
      return instance.get(globalConfig.rootUrl + "weixin/getInfo&openId=" + utils.cache.get("wxConfig").openId);
    } else {
      return new Promise(function(resolve) {
        resolve(store.state.common.wxUserInfo);
      });
    }

  },
  // 
  wxUserInfo(callback) {
    console.log("", store.state.common.wxUserInfo)
    if (!store.state.common.wxUserInfo) {
      return instance.get(globalConfig.rootUrl + "weixin/getInfo1?openId=" + utils.cache.get("wxConfig").openId + "&token=" + utils.cache.get("wxConfig").token);
    } else {
      return new Promise(function(resolve) {
        resolve(store.state.common.wxUserInfo);
      });
    }

  },
  //
  wxConfig() {
    return instance.get(globalConfig.rootUrl + "weixin/getConfig");
  },
  //
  wxPay() {
    function onBridgeReady() {
      WeixinJSBridge.invoke(
        'getBrandWCPayRequest', {
          "appId": "wx4ab795b130ab802e", //
          "timeStamp": new Date().getTime, //1970
          "nonceStr": "e61463f8efa94090b1f366cccfbbb444", //
          "package": "prepay_id=u802345jgfjsdfgsdg888",
          "signType": "MD5", //
          "paySign": "70EA570631E4BB79628FBCA90534C63FF7FADD89" //
        },
        function(res) {
          if (res.err_msg == "get_brand_wcpay_request:ok") {} // ,res.err_msg    ok
        }
      );
    }
    if (typeof WeixinJSBridge == "undefined") {
      if (document.addEventListener) {
        document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
      } else if (document.attachEvent) {
        document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
        document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
      }
    } else {
      onBridgeReady();
    }
  },
  //
  wxClose() {
    WeixinJSBridge && WeixinJSBridge.call('closeWindow');
  }
}


