import validatorFuc from './validatorFuc'
//
const validator = {
  reg: {
    require: /^[\s\S]+$/,
    Positive: /^([1-9]\d*|[0]{1,1})$/, // + 0
    positive: /^[0-9]*[1-9][0-9]*$/, //
    Negative: /^((-d+)|(0+))$/, // + 0
    negative: /^-[0-9]*[1-9][0-9]*$/, //
    integer: /^-?d+$/, //
    PositiveFloat: /^d+(.d+)?$/, // + 0
    positiveFloat: /^(([0-9]+.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*.[0-9]+)|([0-9]*[1-9][0-9]*))$/, //
    NegativeFloat: /^((-d+(.d+)?)|(0+(.0+)?))$/, // + 0
    negativeFloat: /^(-(([0-9]+.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*.[0-9]+)|([0-9]*[1-9][0-9]*)))$/, //
    letter: /^[A-Za-z]+$/, //26
    numberLetter: /^[A-Za-z0-9]+$/,
    mobile: /^(13|14|15|17|18)\d{9}$/, //
    mobileOrEmpty: /^\s*$|^(13|14|15|17|18)\d{9}$/, //()
    telephone: /^((0\d{2,3})-)(\d{7,8})(-(\d{3,}))?$/, //
    chineseLetter: /^[a-z A-Z\u4E00-\u9FA5]+$/, //+
    chinese: /^[\u4E00-\u9FA5]+$/, //
    email: /^([\w-_]+(?:\.[\w-_]+)*)@((?:[a-z0-9]+(?:-[a-zA-Z0-9]+)*)+\.[a-z]{2,6})$/i, //email
    // email: /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/, //email
    carNO: /^[\u4e00-\u9fa5]{1}[A-Z]{1}[A-Z_0-9]{5}$/, //
    pwd: /^[A-Za-z0-9]{6,20}$/, //
    passport: /^[G|E][0-9]{8}$|^[S|P|D][0-9]{7}$/, //
    bankCode: /^[0-9]+$/, //
    // ^1[45][0-9]{7}|([P|p|S|s]\d{7})|([S|s|G|g]\d{8})|([Gg|Tt|Ss|Ll|Qq|Dd|Aa|Ff]\d{8})|([H|h|M|m]\d{810})$
  },
  validate(label, val, ...rules) {
    const result = {
      status: true,
      msg: '',
      results: [],
    }
    for (let rule of rules) {
      const ruleResult = {
        status: true,
        reg: rule.reg,
        msg: ''
      }
      if (rule.isMethods) {
        //
        if (!rule.methods(val)) {
          result.status = false;
          ruleResult.status = false;
          let msg = null;
          if (typeof rule.msg == 'function') {
            msg = rule.msg(label)
          } else {
            msg = rule.msg
          }
          if (result.results.length == 0) {
            result.msg = msg
          }
          ruleResult.msg = msg
        } else {
          ruleResult.status = true;
          ruleResult.data = rule.methods(val);
        }
        result.results.push(ruleResult)
      } else {
        //
        if (!rule.reg.test(val)) {
          result.status = false;
          ruleResult.status = false;
          let msg = null;
          if (typeof rule.msg == 'function') {
            msg = rule.msg(label)
          } else {
            msg = rule.msg
          }
          if (result.results.length == 0) {
            result.msg = msg
          }
          ruleResult.msg = msg
          result.results.push(ruleResult)
        }
      }
    }
    return result
  }
}

validator.rules = {
  require: {
    reg: validator.reg.require,
    msg(label) {
      return (label || '') + ''
    }
  },
  chinese: {
    reg: validator.reg.chinese,
    msg(label) {
      return (label || '') + ''
    }
  },
  number: {
    reg: validator.reg.Positive,
    msg(label) {
      return (label || '') + ''
    }
  },
  mobile: {
    reg: validator.reg.mobile,
    msg(label) {
      return (label || '') + ''
    }
  },
  mobileOrEmpty: {
    reg: validator.reg.mobileOrEmpty,
    msg(label) {
      return (label || '') + ''
    }
  },
  email: {
    reg: validator.reg.email,
    msg(label) {
      return (label || '') + ''
    }
  },
  pwd: {
    reg: validator.reg.pwd,
    msg(label) {
      return (label || '') + ''
    }
  },
  name: {
    reg: validator.reg.chinese,
    msg(label) {
      return (label || '') + ''
    }
  },
  idcard: {
    isMethods: true,
    methods: validatorFuc.isID,
    msg(label) {
      return (label || '') + ''
    }
  },
  passport: {
    reg: validator.reg.passport,
    msg(label) {
      return (label || '') + ''
    }
  },
  bank: {
    reg: validator.reg.bankCode,
    msg(label) {
      return (label || '') + ''
    }
  }
}

export default validator


